* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantarell,
    "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

img {
  display: block;
  max-width: 100%;
}

.timer-input {
  font-weight: bold;
  width: 250px;
  text-align: center;
  font-size: 20px;
  box-shadow: 3rem 2rem 10rem 30rem rgba(96, 130, 241, 0.3);
  position: relative;
  border: 2px solid rgba(37, 64, 151, 0.5);
  border-radius: 10px;
}

.timer-button {
  font-size: 20px;
  font-weight: bold;
  width: 100px;
  text-align: center;
  
  cursor: pointer;
  position: relative;
  z-index: 1;
  border: 3px solid rgba(37, 64, 151, 0.5);
  border-radius: 10px;
  text-transform: uppercase;
}

.timer {
  display: flex;
  width: 250px;
  text-align: center;
}

.value {
  font-weight: bold;
}

.label {
  font-weight: bold;
}